import {useEffect, useState} from 'react'

import axios from 'axios'

// Style
import './HomePage.css'

// layout
import MainLayout from '../../hoc/Layouts/MainLayout/MainLayout'

// Components
import {HeaderNavBar} from '../../components/home/HeaderNavBar/HeaderNavBar'
import {Header} from '../../components/home/Header/Header'
import {DualRing} from '../../components/loaders-animation/DualRing/DualRing'
import {MainContent} from '../../components/home/MainContent/MainContent'

export const HomePage = () => {

    // Данные для связи
    const [contactData, setContactData] = useState([])

    useEffect(() => {
        axios({
            method: 'GET',
            mode: 'cors',
            url: 'http://127.0.0.1:8000/api/contact-data'
            // url: 'http://localhost:3000/api/medical-data'
        }).then(response => {
            setContactData(response.data)
        })
    }, [])

    return (
        <MainLayout>
            <div className="homePage-container">
                {
                    contactData.length !== 0
                        ? <HeaderNavBar data={contactData[0]} />
                        : <DualRing />
                }
                <Header />
                <MainContent />
            </div>
        </MainLayout>
    )
}
